import React from 'react';
import logo from "../static/dojo_logo.png";
import Cube from '../components/Cube';
const About = () => {
    return (
        <>
            <div id="about" className="about__top">
                <div>
                   <h1>About Me</h1>
 
                </div>
                <div className="about__top__flex">
                    <img width="" src={logo} /> 
                    <p className="about__bold">
                       At the Coding Dojo, I set myself apart by passing all three stacks with the Dojo's "Black Belt" requirements. This is a grade that shows that the individual went beyond the curriculum, by researching the framework and it's documentation, to learn and implement new tools for development. 
                   </p>
                </div>
                
            </div>
            <div className="about__bottom">
                <div className="cube__container">
                   <Cube /> 
                </div>
                <p>
                    Since I was young, I've always been infatuated with computers and all they could do. But I wasn't content with just <i>using</i> computers, i had to make them my own.<br />
                    I was always an individual who wanted to take something great, and make it better.<br />
                    I later learned how to build computers and gained a greater understanding of how each component works. I went on to dabble in code in high school, and I'm now persuing my dream as a Software Developer.
                    
                </p>
                
            </div>
        </>
    )
}

export default About
    